@import url(https://fonts.googleapis.com/css?family=Permanent+Marker);
body {
  background-color: #26282C; }

#reactor {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  padding: 25vh 25vw; }

#drum-machine {
  width: auto;
  background-color: #9068BE;
  border-radius: 5px;
  padding: 10px;
  display: -webkit-flex;
  display: flex; }

#container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 20px; }

#pads {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-gap: 10px 10px; }

.drum-pad {
  width: 100%;
  height: 100%;
  border-radius: 5px;
  border: 1px solid blue;
  background-color: blue;
  background-image: -webkit-linear-gradient(#E62739, #F7384A);
  background-image: linear-gradient(#E62739, #F7384A);
  text-decoration: none;
  color: #000;
  font-size: 75px;
  font-family: sans-serif;
  font-weight: 700; }

.inactivePad {
  box-shadow: 1px 1px red; }

.activePad {
  background-color: #6ED3CF;
  color: #FFF;
  box-shadow: 5px 5px #6ED3CF; }

#selectors {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center; }

#display {
  font-family: "Permanent Marker", cursive;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  min-width: 250px;
  min-height: 50px;
  background-color: #E1E8F0;
  border-radius: 5px; }

#sound-selector {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  border: 1px solid black;
  padding: 5px;
  border-radius: 5px; }

.sound-selector-p {
  font-family: "Permanent Marker", cursive;
  font-size: 20px; }

#selector-button {
  background-color: black;
  width: 100%;
  min-height: 20px;
  border: 2px solid black; }

#selector-drums {
  text-align: center;
  background-color: #E62739;
  float: left;
  min-width: 50%;
  min-height: 20px; }

#selector-piano {
  text-align: center;
  background-color: #E62739;
  float: right;
  min-width: 50%;
  min-height: 20px; }

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

